html,
body {
  background-color: #f9f9f9;
  box-sizing: border-box;
  color: #444;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  position: relative;
  scroll-behavior: smooth;
}

main {
  overflow: hidden;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

header {
  background-color: #28324A;
  background-size: cover;
  margin: 0;
  padding: 0;
  position: relative;
  height: 100vh;
  min-height: 600px;
  width: 100%;
}

section {
  margin: 0;
  min-height: 50vh;
  padding: 0;
  width: 100%;
}

footer {
  background-color: var(--blue);
  color: var(--white);
  padding: 30px 0;
}

form {
  margin: 20px auto;
  text-align: center;
}

input {
  margin: 4px 18px 4px 0;
  font-size: 14px;
  color: var(--white);
  background-color: var(--blue);
  border: none;
  border-bottom: 2px solid var(--gold);
  border-left: 2px solid var(--gold);
  border-radius: 0;
  padding: 8px;
  width: 220px;
}

label {
  display: block;
  margin: 0 0 12px;
  padding-bottom: 2px;
}

h1 {
  font-size: calc(64px + (110 - 64) * ((100vw - 300px) / (1400 - 300)));
  font-family: 'Great Vibes', cursive;
  font-weight: 400;
}

h2 {
  font-family: 'Great Vibes', serif;
  font-size: 60px;
  font-size: calc(54px + (66 - 54) * ((100vw - 300px) / (1400 - 300)));
  font-weight: 400;
  margin: 0 0 15px 0;
  text-align: center;
}

p {
  font-family: 'Raleway', sans-serif;
  line-height: 1.418;
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  width: 95%;
}

@media only screen and (min-width: 600px) {
  .grid--2-col  p {
    max-width: 70%;
  }
}

#canvas-image-blending {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  text-align: center;
}

.grid--2-col {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "lft rgt";
  min-height: 480px;
}

.grid-item--lft {
  grid-area: lft;
}

.grid-item--rgt {
  grid-area: rgt;
}

@media only screen and (max-width: 780px) {
  .grid--2-col {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas: "lft"
            "rgt";
  }
}

#map {
  width: 100%;
  height: 380px;
  z-index: 10;
}

#photographs .container {
  padding-top: 60px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, 250px);
  grid-auto-rows: 150px;
  grid-gap: 10px;
  grid-auto-flow: dense;
  justify-content: center;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.gallery-item {
  background-color: #333;
  box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.6);
  overflow: hidden;
}

.gallery-item img {
  width: 100%;
  height: 100%;
  opacity: 0.73;
  object-fit: cover;
}

.img-lg {
  grid-column: span 2;
  grid-row: span 2;
}

.img-tl {
  grid-column: span 1;
  grid-row: span 2;
}

.img-sm {
  grid-column: span 1;
  grid-row: span 1;
}

.footer-nav {
  font-size: 12px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  padding: 0;
}

.footer-content {
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}

nav.main {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Raleway';
  height: 60px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.main-nav {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  display: inline-block;
  margin: 0 8px;
  position: relative;
}

.nav-item a {
  color: #fff;
  font-size: 16px;
  text-decoration: none;
}

.nav-item a::after {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background-color: #D6A319;
  position: absolute;
  left: 50%;
  bottom: -5px;
  transform: translateX(-50%);
  transition: width 0.4s;
}

.nav-item a:hover::after {
  width: 100%;
}

/*-----------------------------------------------------------------------*/

/*-------------------------GENERAL ELMNTS/STYLES-------------------------*/

/*-----------------------------------------------------------------------*/

:root {
  --blue: #28324A;
  --blush: #FFECE5;
  --gold: #D6A319;
  --dark-gold: #CC9C1A;
  --black: #444444;
  --white: #f9f9f9;
}

.btn {
  background-color: var(--gold);
  border-radius: 2px;
  color: var(--white);
  font-size: 16px;
  font-family: sans-serif;
  letter-spacing: 2px;
  margin: 40px auto 0;
  padding: 15px 30px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.2s;
}

.btn-ghost {
  background-color: transparent;
  border: 2px solid var(--gold);
  color: var(--gold);
  margin-top: 20px;
}

.btn:hover {
  background-color: var(--dark-gold);
  color: var(--white);
}

.frm-btn {
  border: none;
  height: auto;
  margin: 4px 0;
  font-size: 14px;
  padding: 8px 16px;
  border: 2px solid var(--gold);
  background-color: transparent;
  color: var(--gold);
}

.hide {
  display: none;
}

.show {
  display: block;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------HEADER------------------------------*/

/*-----------------------------------------------------------------------*/

.names {
  color: #fff;
  font-size: 1vmax;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 28%;
  width: 100%;
  transform: translateY(-50%);
  text-align: center;
}

.and {
  display: inline-block;
  color: var(--gold);
  font-size: 2em;
  transform: translateY(0.15em) rotate(5deg);
  margin: 0 16px 0 0;
}

@media only screen and (max-width: 460px) {
  .and {
    font-size: 72px;
    display: block;
    margin: 0;
    line-height: 0.7em;
    transform: translateY(0px);
  }

  .names {
    top: 30%;
  }
}

.full-date {
  color: var(--white);
  font-size: 24px;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 8px;
  text-align: center;
  position: absolute;
  bottom: 15%;
  left: 50%;
  width: 350px;
  transform: translate(-50%, -50%);
}

.full-date::before {
  background-color: var(--white);
  content: '';
  display: block;
  height: 2px;
  border: none;
  width: 100%;
  position: absolute;
  left: 50%;
  bottom: 1.22em;
  transform: translateX(-50%);
}

.full-date::after {
  background-color: var(--white);
  content: '';
  display: block;
  height: 2px;
  border: none;
  width: 90%;
  position: absolute;
  left: 50%;
  bottom: -4px;
  transform: translateX(-51%);
}

@media only screen and (max-width: 440px) {
  .full-date {
    font-size: 20px;
    line-height: 1.3;
    width: 200px;
    transform: translate(-48%, -55%);
  }

  .full-date::before {
    display: block;
    transform: translateX(-51%);
    top: -4px;
    width: 90%;
  }

  .full-date::after {
    width: 60%;
    bottom: -4px;
  }
}

.down-arrow {
  height: 50px;
  width: 50px;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}

.down-arrow::after {
  content: '';
  background-color: var(--blue);
  width: 95%;
  height: 95%;
  position: absolute;
  top: 1px;
  left: 1px;
  opacity: 0.25;
  transform: rotate(45deg);
  z-index: -1;
  transition: all 0.3s;
}

.down-arrow:hover::after {
  opacity: 0.8;
}

.arrow {
  transition: all 0.2s;
}

.arrow:hover {
  transform: scale(1.2);
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------WELCOME------------------------------*/

/*-----------------------------------------------------------------------*/

.countdown {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.date {
  border-radius: 2px;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 30px 1fr;
  justify-items: center;
  width: 95px;
  height: 90px;
  background-color: var(--blue);
  margin: 60px 10px 0;
  box-shadow: 4px 4px 18px rgba(0, 0, 0, 0.2), 2px 2px 12px rgba(0, 0, 0, 0.5);
}

.date-title {
  color: var(--blush);
  align-self: end;
  font-family: 'Raleway';
  font-size: 16px;
  font-weight: 200;
  padding: 0;
  margin: 0;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}

[class*="date--"] {
  font-family: 'Prata';
  font-size: 48px;
  align-self: end;
  color: var(--gold);
}

@media only screen and (max-width: 680px) {
  .minutes {
    display: none;
  }

  .seconds {
    display: none;
  }
}

@media only screen and (max-width: 480px) {
  .hours {
    display: none;
  }
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------EVENT------------------------------*/

/*-----------------------------------------------------------------------*/

.event-content {
  color: var(--white);
  text-align: center;
}

.event-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 700px) {
  .event-img {
    display: none;
  }
}

.schedule {
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
}

.event {
  letter-spacing: 0.5px;
  margin: 12px 0;
}

